:root {
  --font-body: "Open Sans", sans-serif;
  --font-headings: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pink: hsl(322, 100%, 66%);
  --vy-pale-cyan: hsl(193, 100%, 96%);
  --vy-dk-cyan: hsl(192, 100%, 9%);
  --grayish-blue: hsl(208, 11%, 55%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: var(--vy-dk-cyan);
  font-family: var(--font-body);
  font-size: 1.125rem;
  font-weight: 400;
}

header {
  background-color: var(--vy-pale-cyan);
  background-image: url("../images/bg-hero-mobile.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  margin-bottom: 3.625rem;
}
@media (min-width: 71.875em) {
  header {
    background-image: url("../images/bg-hero-desktop.svg");
    margin-bottom: 9.625rem;
  }
}

main {
  display: grid;
  gap: 2.5rem;
  margin-bottom: 14.9375rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 90rem;
  margin-left: auto;
  margin-right: auto;
}
.container-sm {
  padding: 0 1rem;
}
@media (min-width: 40em) {
  .container-sm {
    padding: 0 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .container-sm {
    padding: 0 4.75rem;
  }
}
.container-md {
  padding: 0 1.25rem;
}
@media (min-width: 40em) {
  .container-md {
    padding: 0 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .container-md {
    padding: 0 6.25rem;
  }
}
.container-lg {
  padding: 0 3.5rem;
}

@keyframes img-appear {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0.3;
  }
  60% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3.75rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
@media (min-width: 40em) {
  .nav {
    padding-top: 2.25rem;
    padding-bottom: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  .nav {
    margin-bottom: 1.125rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.nav__logo {
  display: inline-block;
}
.nav__logo img {
  width: 6.625rem;
  height: 1.125rem;
  transition: all 250ms ease-in;
}
@media (min-width: 40em) {
  .nav__logo img {
    width: 12.6875rem;
    height: 2rem;
  }
}
.nav__cta {
  display: inline-block;
  background-color: var(--white);
  color: var(--vy-dk-cyan);
  border-radius: 3.125rem;
  padding: 0.4375rem 1.4375rem;
  box-shadow: 0 0.125rem 0.625rem #d5d9dd;
  font-size: 0.6875rem;
  font-weight: 700;
  transition: opacity 250ms ease-in;
}
@media (min-width: 40em) {
  .nav__cta {
    padding: 0.95rem 4.2625rem;
    font-size: 0.8125rem;
  }
}
.nav__cta:hover {
  opacity: 0.7;
}

.intro {
  display: grid;
  gap: 3.4375rem;
  place-items: center;
  padding-bottom: 2.3125rem;
}
@media (min-width: 40em) {
  .intro {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .intro {
    grid-template-columns: 1fr 1.4fr;
  }
}
.intro__build {
  text-align: center;
}
@media (min-width: 40em) {
  .intro__build {
    text-align: left;
  }
}
.intro__build h1 {
  font-family: var(--font-headings);
  font-size: 1.5625rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0;
  margin-bottom: 1.75rem;
}
@media (min-width: 71.875em) {
  .intro__build h1 {
    margin-bottom: 2.5rem;
    font-size: 2.6875rem;
  }
}
.intro__build p {
  line-height: 1.5;
  margin-bottom: 2.125rem;
}
.intro__build a {
  display: inline-block;
  box-shadow: 0 0.3125rem 0.625rem #c6ccd2;
  border-radius: 7.5rem;
  padding: 0.78125rem 4.09375rem;
  background-color: var(--pink);
  color: var(--white);
  font-size: 0.6875rem;
  font-weight: 700;
  transition: opacity 300ms ease-in;
}
.intro__build a:hover {
  opacity: 0.7;
}
@media (min-width: 71.875em) {
  .intro__build a {
    padding: 1.1625rem 4.34375rem;
    font-size: 0.875rem;
  }
}
.intro__mockup {
  opacity: 0;
  animation: img-appear;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-fill-mode: forwards;
}
.intro__mockup img {
  width: 100%;
  height: 100%;
}

.feature {
  display: grid;
  gap: 2.4375rem;
  padding: 3.125rem 2.375rem 2.375rem;
  box-shadow: 0 0.1875rem 0.9375rem #cccccc;
  border-radius: 1.25rem;
}
@media (min-width: 40em) {
  .feature {
    padding: 6.25rem 2.1875rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .feature__illustration-alt {
    order: 2;
  }
  .feature__content-alt {
    order: 1;
  }
}
@media (min-width: 71.875em) {
  .feature {
    padding: 3.125rem 2.1875rem 3.125rem 7.1875rem;
    gap: 8.75rem;
  }
}
.feature__illustration img {
  width: 100%;
  height: 100%;
}
.feature__content {
  place-self: center;
  text-align: center;
}
@media (min-width: 40em) {
  .feature__content {
    text-align: left;
  }
}
.feature__content h2 {
  margin-bottom: 1.3125rem;
  font-family: var(--font-headings);
  font-size: 1.3125rem;
}
@media (min-width: 71.875em) {
  .feature__content h2 {
    margin-bottom: 1.125rem;
    font-size: 1.8125rem;
  }
}
.feature__content p {
  color: var(--grayish-blue);
  line-height: 1.3;
  font-size: 0.9375rem;
}
@media (min-width: 71.875em) {
  .feature__content p {
    font-size: 1.125rem;
  }
}

footer {
  background-color: var(--vy-dk-cyan);
}

.footer {
  padding-top: 8.625rem;
  padding-bottom: 2.9375rem;
  position: relative;
}
@media (min-width: 40em) {
  .footer {
    padding-top: 11.875rem;
  }
}
.footer-build {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: 90%;
  max-width: 50rem;
  transform: translate(-50%, 50%);
  border-radius: 0.9375rem;
  box-shadow: 0 0.0625rem 0.3125rem #2e2e2e;
  padding: 2.875rem 0;
  background-color: var(--white);
  text-align: center;
}
.footer-build p {
  margin-bottom: 1.5625rem;
  font-family: var(--font-heading);
  font-weight: 600;
}
@media (min-width: 40em) {
  .footer-build p {
    margin-bottom: 2.4375rem;
    font-size: 2rem;
  }
}
.footer-build a {
  display: inline-block;
  border-radius: 6.25rem;
  padding: 0.6875rem 3.1875rem;
  color: var(--white);
  background-color: var(--pink);
  font-weight: 700;
  font-size: 0.625rem;
  transition: opacity 300ms ease-in;
}
@media (min-width: 40em) {
  .footer-build a {
    font-size: 1rem;
    padding: 1.8125rem 7.5rem;
  }
}
.footer-build a:hover {
  opacity: 0.7;
}
.footer-logo {
  display: inline-block;
  margin-bottom: 1.75rem;
}
.footer-logo img {
  width: 10.5rem;
  height: 1.75rem;
}
@media (min-width: 71.875em) {
  .footer-logo img {
    width: 15.3125rem;
    height: 2.5rem;
  }
}
.footer-copyright {
  color: var(--white);
  font-size: 0.8125rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .footer-copyright {
    font-size: 0.9375rem;
    text-align: right;
  }
}

.info {
  display: grid;
  gap: 3.4375rem;
  color: var(--white);
  font-size: 1rem;
}
@media (min-width: 71.875em) {
  .info {
    grid-template-columns: 1fr 1fr 0.5fr;
  }
}
.info__contact {
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
  font-size: 1.25rem;
}
.info__contact div {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5625rem;
}
.info__contact div a {
  transition: color 200ms ease-in;
}
.info__contact div a:hover {
  color: var(--pink);
}
.info__contact--loc {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--loc ~ span {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--num {
  color: var(--white);
}
.info__contact--email {
  color: var(--white);
}
.info__links {
  display: grid;
  gap: 1.75rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 1.125rem;
}
.info__links a {
  color: var(--white);
  transition: text-decoration 300ms ease-in;
}
.info__links a:hover {
  text-decoration: underline;
}
@media (min-width: 71.875em) {
  .info__links {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: max-content;
    gap: 1.5rem;
  }
  .info__links--us {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .info__links--we {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .info__links--faq {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .info__links--career {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .info__links--blog {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .info__links--contact {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
.info__socmed {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
  margin-bottom: 1.5625rem;
}
@media (min-width: 71.875em) {
  .info__socmed {
    justify-content: flex-start;
  }
}
.info__socmed a {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.0625rem solid var(--white);
  border-radius: 50%;
  width: 2.0625rem;
  height: 2.0625rem;
  color: var(--white);
  font-size: 1.125rem;
  transition: all 200ms ease-in;
}
.info__socmed a:hover {
  border-color: var(--pink);
  color: var(--pink);
}